@import "../../assets/styles/variable.scss";

.controlArea {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 40px 0 5px 0;
}
.volumeContainer {
  display: flex;
  align-items: center;

  input {
    &[type="range"] {
      width: 75px;
      -webkit-appearance: none;
      height: 4px;
      background-color: $orange;
      outline-color: transparent;
    }

    &:focus {
      outline-color: transparent;
    }

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      width: 12px;
      height: 12px;
      background-color: $orange;
      border-radius: 12px;
      overflow: visible;
    }
  }
}
